<template>
  <v-form>
    <v-container>
      <v-layout row wrap>
        <v-flex xs12>
          <transition name="custom-classes-transition" enter-active-class="animated fadeIn">
            <v-alert :value="true" type="error" v-if="error.word.required">単語を入力してください。</v-alert>
          </transition>

          <transition name="custom-classes-transition" enter-active-class="animated fadeIn">
            <v-alert :value="true" type="error" v-if="error.definition.required">定義を入力してください。</v-alert>
          </transition>

          <transition name="custom-classes-transition" enter-active-class="animated fadeIn">
            <v-alert :value="true" type="info" v-if="remains != 0 && completedCardNum > remains">
              {{ cards.length }} 件の単語のうち、
              {{ cards.length-remains }} 件を覚えました！
            </v-alert>
          </transition>

          <transition name="custom-classes-transition" enter-active-class="animated tada">
            <v-alert :value="true" type="success" v-if="remains == 0">全部覚えました！素晴らしい！＼(^O^)／</v-alert>
          </transition>
        </v-flex>

        <v-flex xs12>
          <div id="addForm">
            <v-form>
              <v-container>
                <h1 v-bind:class="{'heading': $vuetify.breakpoint.xs, 'display-1': $vuetify.breakpoint.smAndUp}">新しい単語を追加</h1>
                <form @submit.prevent="addCard">
                  <v-layout row wrap>
                    <v-flex xs12 sm3>
                      <v-text-field label="単語" v-model="newCard.word"></v-text-field>
                    </v-flex>

                    <v-flex xs12 sm5>
                      <v-text-field label="定義" v-model="newCard.definition"></v-text-field>
                    </v-flex>

                    <v-flex xs12 sm4 class="text-xs-center text-sm-left">
                      <v-btn fab dark large type="submit" color="#D74B4B">
                        <v-icon dark>add</v-icon>
                      </v-btn>
                    </v-flex>
                  </v-layout>
                </form>
              </v-container>
            </v-form>
          </div>
        </v-flex>

        <v-flex xs12>
          <table id="list">
            <thead>
              <tr>
                <th @click="setting.showWords = !setting.showWords;">単語</th>
                <th @click="setting.showDefinition = !setting.showDefinition;">定義</th>
              </tr>
            </thead>
            <tbody>
              <tr
                  v-for="(card, index) in cards"
                  v-bind:key="index"
                  v-bind:class="{ 'hidden': !setting.showCompletedWords && card.isCompleted}"
                  v-on:click="showDialog(index)">
                <td>
                  <span
                        v-bind:class="{ 'complete': card.isCompleted, 'incomplete': !card.isCompleted, 'transparent': !setting.showWords }">{{ card.word }}</span>
                </td>
                <td>
                  <span
                        v-bind:class="{ 'complete': card.isCompleted, 'incomplete': !card.isCompleted, 'transparent': !setting.showDefinition }">{{ card.definition }}</span>
                </td>
              </tr>
            </tbody>
          </table>
        </v-flex>

        <v-dialog v-model="dialog.show" width="700px">
          <v-card>
            <v-card-title class="headline grey lighten-2" primary-title>
              <v-icon class="mr-2" color="#D74B4B" v-if="cards[dialog.index].isCompleted === true">check_box</v-icon>
              <v-icon class="mr-2" color="#D74B4B" v-if="cards[dialog.index].isCompleted === false">check_box_outline_blank</v-icon>
              <span
                    v-bind:class="{ 'transparent': !setting.showWords }">{{ cards[dialog.index].word }}</span>
              <v-spacer></v-spacer>
              <v-chip v-if="isNextCard() === true" dark color="#D74B4B" v-on:click="showDialog(dialog.index+1);">
                <v-icon>arrow_forward</v-icon>
              </v-chip>
              <v-chip v-else dark color="#D74B4B" v-on:click="showDialog(0);">
                <v-icon>first_page</v-icon>
              </v-chip>
            </v-card-title>

            <v-card-text>
              <span
                    v-bind:class="{ 'transparent': !setting.showDefinition }">{{ cards[dialog.index].definition }}</span>
            </v-card-text>

            <v-divider></v-divider>

            <v-card-actions>
              <v-spacer></v-spacer>
              <v-icon v-on:click="deleteCard(dialog.index)" color="#D74B4B">delete</v-icon>
            </v-card-actions>
          </v-card>
        </v-dialog>
      </v-layout>
    </v-container>
  </v-form>
</template>

<script>
  // CompleteCardメソッドを追加、メッセージを変える
  // 試験機能。StartTrialメソッドを追加、ダイアログを表示しIndexを0にする
  // 発音機能
  export default {
    data: () => ({
      dialog: {
        show: false,
        index: 0,
      },
      setting: {
        showCompletedWords: true,
        showWords: true,
        showDefinition: true,
        showEdit: true
      },
      newCard: { word: "", definition: "", isCompleted: false },
      cards: [
        {
          word: "Deploy or Die",
          definition: "デプロイせよ、さもなくば死だ。",
          isCompleted: false
        },
        {
          word: "Disrupt",
          definition:
            "〈会合･制度･交通など〉を混乱[中断]させる, 存続不可能にする, 崩壊[分裂]させる",
          isCompleted: true
        },
        {
          word: "Cat",
          definition:
            "A cat is a furry animal that has a long tail and sharp claws.",
          isCompleted: false
        }
      ],
      error: {
        word: {
          required: false
        },
        definition: {
          required: false
        }
      },
      completedCardNum: 0
    }),
    methods: {
      isNextCard: function () {
        if (this.cards.length != this.dialog.index + 1) {
          return true;
        } else {
          return false;
        }
      },
      addCard: function () {
        this.error.word.required = this.newCard.word.length < 1 ? true : false;
        this.error.definition.required =
          this.newCard.definition.length < 1 ? true : false;

        if (
          this.error.word.required !== false ||
          this.error.definition.required !== false
        ) {
          return;
        }

        this.cards.unshift({
          word: this.newCard.word,
          definition: this.newCard.definition,
          isCompleted: false
        });

        this.newCard = { word: "", definition: "", isCompleted: false };
        this.saveCards();
      },
      deleteCard: function (index) {
        this.cards.splice(index, 1);
        this.saveCards();
      },
      updateCard: function (index, key, val) {
        let newCard = this.cards[index];
        newCard[key] = val;
        this.cards.splice(index, 1, newCard);
        this.saveCards();
      },
      saveCards: function () {
        localStorage.setItem("cards", JSON.stringify(this.cards));
      },
      showDialog: function (index) {
        this.dialog.show = true;
        this.dialog.index = index;
        let uttr = new SpeechSynthesisUtterance(this.cards[this.dialog.index].word);
        uttr.lang = "en-US";
        speechSynthesis.speak(uttr);
      }
    },
    watch: {
      newCard: {
        handler(val) {
          // console.log(val); // Deep Watcher
          val;
        },
        deep: true
      }
    },
    mounted() {
      let counter = 0;

      if (localStorage.getItem("cards")) {
        try {
          this.cards = JSON.parse(localStorage.getItem("cards"));
        } catch (e) {
          localStorage.removeItem("cards");
        }
      }

      for (let i = 0; i < this.cards.length; i++) {
        if (!this.cards[i].isCompleted) {
          counter++;
        }
      }

      this.completedCardNum = counter;
    },
    computed: {
      remains: function () {
        let counter = 0;
        for (let i = 0; i < this.cards.length; i++) {
          if (!this.cards[i].isCompleted) {
            counter++;
          }
        }
        return counter;
      }
    }
  };
</script>

