  *.hidden {
    display: none;
  }

  *.transparent {
    color: rgba(0, 0, 0, 0) !important;
    transition-duration: 0.5s;
  }

  *.complete {
    color: #bbb;
  }

  div#addForm h1 {
    color: #354b5e;
  }

  table#list {
    width: 100%;
    border-radius: 8px;
    background: #fff;
  }

  table#list tr.hidden {
    display: none;
  }

  table#list tr {
    transition-duration: 0.2s;
    cursor: pointer;
  }

  table#list tr:hover {
    background: #f2f2f2;
  }

  table#list th {
    padding: 12px 16px;
    color: #fff;
    background: #475f77;
  }

  table#list th label {
    color: #fff;
  }

  table#list th:nth-of-type(1) {
    border-radius: 8px 0px 0px 0px;
  }

  table#list th:nth-of-type(2) {
    border-radius: 0px 8px 0px 0px;
  }

  table#list th:nth-of-type(1),
  table#list th:nth-of-type(2) {
    transition-duration: 0.2s;
    cursor: pointer;
  }

  table#list th:nth-of-type(1):hover,
  table#list th:nth-of-type(2):hover {
    color: #fff;
    background: #d74b4b;
  }

  table#list td {
    padding: 16px;
  }

  table#list tr:last-child td {
    border-style: none;
  }

  table#list td span {
    transition-duration: 0.5s;
  }

  table#list td:nth-of-type(1) span.incomplete {
    font-weight: 700;
    color: #d74b4b;
  }

  table#list td:nth-of-type(2) span.incomplete {
    color: #354b5e;
  }
</style>
